$(function() {
  $('.menu__list a').on('click', function(event) {
    event.preventDefault();
    var id = $(this).attr('href'), top = $(id).offset().top;
    $('body,html').animate({scrollTop: top}, 1500);
  });

  $('.header__menu-burger').on('click', function() {
    $(this).toggleClass('open-menu');
    $('.menu').toggleClass('open-menu');
    $('body').toggleClass('fixed-page');
  });

  $('.top-slider__items').slick({
    prevArrow: '<button class="slick-prev" type="button"><svg width="7" height="10" viewBox="0 0 7 10" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M6 1L2 5L6 9" stroke="none" stroke-width="2"/></svg></button>',
    nextArrow: '<button class="slick-next" type="button"><svg width="7" height="11" viewBox="0 0 7 11" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M1 1L5 5.5L1 10" stroke="none" stroke-width="2"/></svg></button>',
    infinite: false,
    slidesToShow: 3,
    slidesToScroll: 1,
    responsive: [
      {
        breakpoint: 1367,
        settings: {
          slidesToShow: 2,
        }
      },
      {
        breakpoint: 990,
        settings: {
          arrows: false,
          slidesToShow: 2,
          dots: true,
        }
      },
      {
        breakpoint: 769,
        settings: {
          arrows: false,
          slidesToShow: 1,
          dots: true,
        }
      }
    ]
  });

  $('.reviews-slider').slick({
    prevArrow: '<button class="slick-prev" type="button"><svg width="7" height="10" viewBox="0 0 7 10" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M6 1L2 5L6 9" stroke="none" stroke-width="2"/></svg></button>',
    nextArrow: '<button class="slick-next" type="button"><svg width="7" height="11" viewBox="0 0 7 11" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M1 1L5 5.5L1 10" stroke="none" stroke-width="2"/></svg></button>',
    infinite: false,
    slidesToShow: 2,
    slidesToScroll: 1,
    responsive: [
      {
        breakpoint: 1420,
        settings: {
          slidesToShow: 1,
          dots: true,
        }
      },
      {
        breakpoint: 990,
        settings: {
          arrows: false,
          slidesToShow: 1,
          dots: true,
        }
      },
    ]
  });

  $(document).scroll(function(e) {
    $(window).scrollTop() > 1 ? $('.menu__wrapper').addClass('menu--bg') : $('.menu__wrapper').removeClass('menu--bg');
    $(window).scrollTop() > 1 ? $('.header__logo').addClass('header__logo-change') : $('.header__logo').removeClass('header__logo-change');
  });
})